:root {
  --thin-border-color: #bfc6d4;
  --thick-border-color: #344860;
  --thick-border: 2px;
  --selected-color: #bcdefb;
  --sibling-selected-color: #c3d7ea;
  --highlighted-color: #e2ebf3;
  --invalid-color: #f7cfd6;
  --player-number-color: #0073e6;
  --text-color: #344860;
  --cell-size: -1px;
}

#root {
  grid-template-areas: "header"
                       "board"
                       "controls"
                       "numpad";
  justify-content: center;
  row-gap: .3em;
  font-family: Segoe UI, Tahoma, Geneva, Verdana, sans-serif;
  font-weight: 200;
  display: grid;
}

#root .header {
  flex-direction: row;
  font-size: .7em;
  display: flex;
}

#root .header .difficulty {
  flex: 1;
}

#root .board {
  color: var(--text-color);
  border: var(--thick-border) solid var(--thick-border-color);
  border-collapse: collapse;
  -webkit-user-select: none;
  user-select: none;
}

#root .board .cell {
  width: var(--cell-size);
  height: var(--cell-size);
  text-align: center;
  font-size: calc(var(--cell-size) / 1.5);
  border: 1px solid var(--thin-border-color);
}

#root .board .cell.highlighted {
  background-color: var(--highlighted-color);
}

#root .board .cell.selected {
  background-color: var(--selected-color);
}

#root .board .cell.sibling-selected {
  background-color: var(--sibling-selected-color);
}

#root .board .cell.player-value {
  color: var(--player-number-color);
}

#root .board .cell.invalid {
  background-color: var(--invalid-color);
}

#root .board .cell .placeholders {
  grid-gap: 0px;
  grid-template-columns: repeat(3, calc(var(--cell-size) / 3) );
  grid-template-rows: repeat(3, calc(var(--cell-size) / 3) );
  font-size: calc(var(--cell-size) / 5);
  grid-template-areas: "a1 a2 a3"
                       "a4 a5 a6"
                       "a7 a8 a9";
  display: grid;
}

#root .board .cell[data-col="3"], #root .board .cell[data-col="6"], #root .board .cell[data-col="9"] {
  border-right-width: var(--thick-border);
  border-right-color: var(--thick-border-color);
}

#root .board .cell[data-row="3"], #root .board .cell[data-row="6"], #root .board .cell[data-row="9"] {
  border-bottom-width: var(--thick-border);
  border-bottom-color: var(--thick-border-color);
}

#root .controls {
  column-gap: .5em;
  margin-left: auto;
  display: flex;
}

#root .controls .button {
  width: 2em;
  height: 2em;
  background-color: #dde4ee;
  border: 1px solid #0000;
  border-radius: 2em;
  padding: .5em;
  transition: background-color .5s ease-in-out;
}

#root .controls .button svg {
  fill: var(--thick-border-color);
}

#root .controls .button.active {
  border: 1px solid var(--thick-border-color);
  background-color: var(--thin-border-color);
}

#root .numpad {
  display: none;
}

@media only screen and (max-width: 400px) {
  #root .numpad {
    color: var(--player-number-color);
    -webkit-user-select: none;
    user-select: none;
    justify-content: space-around;
    font-size: 3em;
    display: flex;
  }

  #root .numpad > div {
    cursor: pointer;
  }
}

